@import url('https://fonts.googleapis.com/css2?family=Zen+Kaku+Gothic+New&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: var(--main-bg-color);
}

:root {
  --main-font: "Zen Kaku Gothic New", serif;

  /*色系*/
  --gray50: #f6f7f8;
  /*月白*/
  --gray100: #e8e8e8;
  /*灰色*/
  --purple: #be84b8;
  /*若紫*/
  --black: #212121;
  /*漆黒*/
  --white: #ffffff;
  
  /* プライマリな色*/
  --main-color: var(--black);
  --main-bg-color: var(--gray50);

  /*サイズ系変数*/
  --margin-sm: 8px;
  --margin-md: 16px;
  --margin-lg: 24px;
  --margin-xl: 32px;
  --margin-xxl: 64px;

  --padding-sm: 4px;
  --padding-md: 8px;
  --padding-lg: 16px;
  --padding-xl: 24px;
  --padding-xxl: 32px --button-height: 44px;

  --header-height: 48px;

  --fontsize-sm: 12px;
  --fontsize-md: 16px;
  --fontsize-lg: 20px;
  --fontsize-xl: 24px;

  --borderradius-sm: 4px;
  --borderradius-md: 8px;
  --borderradius-lg: 12px;
}

body {
  height: 100vh;
  background-color: var(--gray50);
  display: flex;
  flex-direction: column;
  padding-top: 0;
  padding-right: var(--padding-lg);
  padding-left: var(--padding-lg);
  z-index: 0;
  font-family: var(--main-font);
}

/*ここからテキスト*/
h1 {
  font-family: var(--main-font);
  color: var(--main-color);
}

/*ここからコンポーネント*/
button{
  min-height: 44px;
  min-width: 44px;
  border: none;
}

header {
  height: var(--header-height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--padding-sm);
}

.header-h1 {
  font-size: var(--fontsize-sm);
}

.header-box {
  flex-grow: 1;
  height: var(--fontsize-sm);
  border: 1px solid var(--main-color);
}

.modal-overlay {
  position: fixed;
  top: var(--header-height);
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--main-bg-color);
  display: flex;
  flex-direction: column;
  gap: var(--margin-md);
  align-items: center;
  z-index: 1000;
}

.projectListContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--margin-md);
}

.tagButtonContainer{
  width: 100%;
  display: flex;
  justify-content: left;
  gap: var(--margin-md);
}

.tagButton{
  background-color: var(--white);
  padding: var(--padding-lg), var(--padding-sm);
}

.tagButtonSelected{
  background-color: var(--black);
  color: var(--white);
  padding: var(--padding-lg), var(--padding-sm);
}

.projectCard{
  width:100%;
  padding: var(--padding-md);
  display: flex;
  flex-direction: column;
  gap: var(--margin-md);
  border-radius: var(--borderradius-md);
  background-color: var(--white);
}

.projectDetailContainer{
  display: flex;
  flex-direction: column;
  gap: var(--margin-md);
  align-items: center;
}

.projectDetailImage{
  height:60px;
  width: 100%;
  position: relative;
}